.hiddenMenusearch{
    width: 98%;
     visibility: hidden; 
    position: absolute;
    top: 9.3rem;
    left: 1rem;
    right: 2rem;
    margin: auto;
    display: flex;
    background-color: #f3f3f3;
    align-items: center;
    justify-content: center;
    height: 7rem;
    border-radius: 0.5rem;
     opacity: 0; 
    input{
        height: 2rem;
        display: flex;
         width: 28%;
        border: none;
        background-color: #f3f3f3;
        margin: 0 1rem;
        font-size: 1.2rem;
        outline: none;
       &:focus{
           border-bottom: 1px solid black;
          
       }
        &::placeholder{
            font-size: 1.2rem;
            }
            
       
    }
    a{
        color: #777;
    }
}